    width: 47.5%;
    height: 100%;
    border: 2px solid red;
    border-radius: 5px;
    font-size: 1.5vh;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
    color: red;
}

@media screen and (min-width: 700px){
    .FermeriDeleteProductContentBox {
        position: relative;
        margin-left: auto;
        margin-right: auto;
        width: 60%;
        max-width: 350px;
        display: flex;
        flex-direction: column;
    }
}

@media screen and (min-width: 1100px){
    .FermeriDeleteProductContentBox {
        position: relative;
        margin-left: auto;
        margin-right: auto;
        width: 60%;
        max-width: 250px;
        display: flex;
        flex-direction: column;
    }
}
